
body{margin:0;color:rgba(255,255,255,0.7)
}
.app{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;width:100vw;height:100vh;font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:inherit
}
button,a[role=button]{background-color:transparent;border:none;color:rgba(255,255,255,0.7);cursor:pointer;font-size:1.2em;text-decoration:none;font-weight:bold;text-transform:uppercase;padding:1em;transition:background-color .2s
}
button:hover,button:focus,a[role=button]:hover,a[role=button]:focus{background-color:rgba(0,0,0,0.5);outline:none
}



